"use client";

import { useState } from "react";
import { Plus, Trash2, ChevronLeft, ChevronRight } from "lucide-react";

interface PageData {
    id: string;
    name: string;
    canvasData: any;
}

interface SidebarProps {
    pages: PageData[];
    activePageId: string;
    onSelectPage: (id: string) => void;
    onAddPage: () => void;
    onRenamePage: (id: string, newName: string) => void;
    onDeletePage: (id: string) => void;
}

export default function PageSidebar({
    pages,
    activePageId,
    onSelectPage,
    onAddPage,
    onRenamePage,
    onDeletePage,
}: SidebarProps) {
    const [collapsed, setCollapsed] = useState(false);
    const [editingId, setEditingId] = useState<string | null>(null);
    const [editValue, setEditValue] = useState("");

    return (
        <aside
            className={`h-full border-r border-gray-300 bg-gray-100 transition-all duration-300
        ${collapsed ? "w-14" : "w-56"}`}
        >
            {/* Collapse toggle */}
            <div className="flex items-center justify-between p-2 border-b border-gray-300">
                {!collapsed && (
                    <span className="text-[13px] font-semibold text-gray-800 uppercase tracking-wide">
                        Pages
                    </span>
                )}
                <button
                    onClick={() => setCollapsed(!collapsed)}
                    className="p-1 hover:bg-gray-200 rounded-md"
                >
                    {collapsed ? (
                        <ChevronRight className="w-4 h-4 text-gray-700" />
                    ) : (
                        <ChevronLeft className="w-4 h-4 text-gray-700" />
                    )}
                </button>
            </div>

            {/* Pages list */}
            <ul className="overflow-y-auto h-[calc(100%-3rem)]">
                {pages.map((page) => (
                    <li
                        key={page.id}
                        className={`group flex items-center justify-between px-3 py-1.5 cursor-pointer text-[13px]
              ${activePageId === page.id
                                ? "bg-gray-200 font-semibold text-gray-900 border-l-2 border-blue-500"
                                : "hover:bg-gray-200 text-gray-800"
                            }`}
                        onClick={() => onSelectPage(page.id)}
                        onDoubleClick={() => {
                            setEditingId(page.id);
                            setEditValue(page.name);
                        }}
                    >
                        {editingId === page.id ? (
                            <input
                                value={editValue}
                                onChange={(e) => setEditValue(e.target.value)}
                                onBlur={() => {
                                    onRenamePage(page.id, editValue.trim() || "Untitled");
                                    setEditingId(null);
                                }}
                                onKeyDown={(e) => {
                                    if (e.key === "Enter") {
                                        onRenamePage(page.id, editValue.trim() || "Untitled");
                                        setEditingId(null);
                                    }
                                }}
                                autoFocus
                                className="flex-1 bg-white border border-gray-300 rounded px-1 text-[13px] text-gray-900"
                            />
                        ) : (
                            <span className="truncate flex-1">{page.name}</span>
                        )}
                        {!collapsed && (
                            <button
                                onClick={(e) => {
                                    e.stopPropagation();
                                    onDeletePage(page.id);
                                }}
                                className="opacity-0 group-hover:opacity-100 p-1 hover:bg-red-100 rounded-md"
                            >
                                <Trash2 className="w-4 h-4 text-gray-500 hover:text-red-600" />
                            </button>
                        )}
                    </li>
                ))}
            </ul>

            {/* Add page button */}
            {!collapsed && (
                <div className="p-2 border-t border-gray-300">
                    <button
                        onClick={onAddPage}
                        className="w-full flex items-center gap-1 justify-center px-2 py-1.5 text-[13px] bg-gray-200 hover:bg-gray-300 rounded-md text-gray-800 font-medium"
                    >
                        <Plus className="w-4 h-4" /> New Page
                    </button>
                </div>
            )}
        </aside>
    );
}
